  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.modal-header h3 {
  color: #00d4aa;
  font-size: 1.5rem;
}

.modal-close {
  background: none;
  border: none;
  color: #e0e0e0;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.modal-close:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #ff6b6b;
}

/* Form Styling */
.skill-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  color: #00d4aa;
  font-weight: bold;
}

.form-group input,
.form-group textarea,
.form-group select {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #e0e0e0;
  padding: 0.8rem;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: #00d4aa;
  box-shadow: 0 0 10px rgba(0, 212, 170, 0.3);
}

.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  margin-top: 1rem;
}

.btn-primary,
.btn-secondary {
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
}

.btn-primary {
  background: linear-gradient(45deg, #00d4aa, #64ffda);
  color: white;
  box-shadow: 0 4px 15px rgba(0, 212, 170, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 212, 170, 0.4);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: #e0e0e0;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.2);
}

/* Health Page Styling */
.health-page {
  animation: fadeIn 0.5s ease-in-out;
}

.health-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.health-header h2 {
  color: #ff6b6b;
  font-size: 2rem;
  text-shadow: 0 0 10px rgba(255, 107, 107, 0.5);
}

.health-status {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.status-indicator {
  background: rgba(255, 107, 107, 0.1);
  border: 1px solid rgba(255, 107, 107, 0.3);
  padding: 0.8rem 1.5rem;
  border-radius: 10px;
  backdrop-filter: blur(5px);
}

.status-label {
  color: #b0b0b0;
  margin-right: 0.5rem;
}

.status-value {
  color: #ff6b6b;
  font-weight: bold;
  font-size: 1.2rem;
}

.health-nav {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.health-tab {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 107, 107, 0.3);
  color: #e0e0e0;
  padding: 0.8rem 1.5rem;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.health-tab:hover {
  background: rgba(255, 107, 107, 0.1);
  border-color: rgba(255, 107, 107, 0.5);
}

.health-tab.active {
  background: linear-gradient(45deg, rgba(255, 107, 107, 0.2), rgba(255, 107, 107, 0.1));
  border-color: #ff6b6b;
  color: #ff6b6b;
}

/* Health Metrics */
.health-metrics {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.metric-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 107, 107, 0.2);
  border-radius: 12px;
  padding: 1.5rem;
  backdrop-filter: blur(10px);
}

.metric-card h3 {
  color: #ff6b6b;
  margin-bottom: 1rem;
  text-align: center;
  text-shadow: 0 0 10px rgba(255, 107, 107, 0.3);
}

.metric-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.metric-row label {
  color: #b0b0b0;
  font-weight: bold;
  min-width: 120px;
}

.metric-row input {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #e0e0e0;
  padding: 0.5rem;
  border-radius: 6px;
  max-width: 100px;
}

.bp-inputs {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.bp-inputs span {
  color: #ff6b6b;
  font-weight: bold;
}

.slider-container {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex: 1;
}

.wellness-slider {
  flex: 1;
  height: 6px;
  border-radius: 3px;
  outline: none;
  background: rgba(255, 255, 255, 0.1);
}

.wellness-slider.stress::-webkit-slider-thumb,
.wellness-slider.energy::-webkit-slider-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: linear-gradient(45deg, #ff6b6b, #ff8e8e);
  cursor: pointer;
  box-shadow: 0 0 10px rgba(255, 107, 107, 0.5);
}

.slider-value {
  color: #ff6b6b;
  font-weight: bold;
  min-width: 20px;
  text-align: center;
}

.metric-result {
  text-align: center;
  margin-top: 1rem;
  padding: 0.8rem;
  background: rgba(255, 107, 107, 0.1);
  border-radius: 8px;
}

/* Export Page Styling */
.export-page {
  animation: fadeIn 0.5s ease-in-out;
}

.export-header h2 {
  color: #ffd700;
  font-size: 2rem;
  text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
  margin-bottom: 0.5rem;
}

.format-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

.format-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 215, 0, 0.3);
  border-radius: 10px;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
}

.format-card:hover {
  background: rgba(255, 215, 0, 0.1);
  border-color: rgba(255, 215, 0, 0.5);
}

.format-card.selected {
  background: linear-gradient(45deg, rgba(255, 215, 0, 0.2), rgba(255, 215, 0, 0.1));
  border-color: #ffd700;
}

.format-card h4 {
  color: #ffd700;
  margin-bottom: 0.5rem;
}

.export-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin: 2rem 0;
  flex-wrap: wrap;
}

.btn-ai {
  background: linear-gradient(45deg, #9c27b0, #e91e63);
  color: white;
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(156, 39, 176, 0.3);
}

.btn-ai:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(156, 39, 176, 0.4);
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes drawCircle {
  from {
    stroke-dasharray: 0 1000;
  }
  to {
    stroke-dasharray: var(--progress) 1000;
  }
}

@keyframes sparkle {
  0%, 100% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.2);
    opacity: 0.8;
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .app-content {
    padding: 0;
  }
  
  .app-title {
    font-size: 2rem;
  }
  
  .tab-navigation {
    gap: 0.5rem;
    padding: 0 0.5rem;
  }
  
  .tab-button {
    padding: 0.6rem 1rem;
    font-size: 0.9rem;
    border-radius: 8px;
  }
  
  .tab-button:hover {
    transform: none;
  }
  
  .tab-icon {
    font-size: 1rem;
  }
  
  .skills-header {
    flex-direction: column;
    text-align: center;
  }
  
  .skills-stats {
    justify-content: center;
  }
  
  .skills-list {
    grid-template-columns: 1fr;
  }
  
  .metrics-grid {
    grid-template-columns: 1fr;
  }
  
  .export-actions {
    flex-direction: column;
    align-items: center;
  }
}

