 0.5rem;\r\n  }\r\n  \r\n  .welcome-text {\r\n    font-size: 0.9rem;\r\n  }\r\n  \r\n  .logout-button {\r\n    padding: 6px 12px;\r\n    font-size: 0.85rem;\r\n  }\r\n}\r\n\r\n.user-info {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n}\r\n\r\n.welcome-text {\r\n  color: #00d4aa;\r\n  font-weight: bold;\r\n}\r\n\r\n.logout-button {\r\n  background: linear-gradient(45deg, #ff6b6b, #ff8a80);\r\n  border: none;\r\n  color: white;\r\n  padding: 8px 16px;\r\n  border-radius: 6px;\r\n  cursor: pointer;\r\n  font-weight: bold;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.logout-button:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.4);\r\n}\r\n\r\n.app-title {\r\n  font-size: 1.25rem;\r\n  font-weight: bold;\r\n  background: linear-gradient(45deg, #ff6b6b, #4ecdc4, #45b7d1, #96ceb4, #feca57, #ff9ff3, #54a0ff);\r\n  background-size: 400% 400%;\r\n  background-clip: text;\r\n  -webkit-background-clip: text;\r\n  -webkit-text-fill-color: transparent;\r\n  animation: colorWave 3s ease-in-out infinite;\r\n  text-shadow: 0 0 20px rgba(0, 212, 170, 0.5);\r\n}\r\n\r\n@keyframes colorWave {\r\n  0% {\r\n    background-position: 0% 50%;\r\n  }\r\n  50% {\r\n    background-position: 100% 50%;\r\n  }\r\n  100% {\r\n    background-position: 0% 50%;\r\n  }\r\n}\r\n\r\n/* Loading Screen */\r\n.loading-screen {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  min-height: 100vh;\r\n  text-align: center;\r\n}\r\n\r\n.loading-screen h1 {\r\n  font-size: 1.25rem;\r\n  font-weight: bold;\r\n  background: linear-gradient(45deg, #ff6b6b, #4ecdc4, #45b7d1, #96ceb4, #feca57, #ff9ff3, #54a0ff);\r\n  background-size: 400% 400%;\r\n  background-clip: text;\r\n  -webkit-background-clip: text;\r\n  -webkit-text-fill-color: transparent;\r\n  animation: colorWave 3s ease-in-out infinite;\r\n  text-shadow: 0 0 20px rgba(0, 212, 170, 0.5);\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n.loading-spinner {\r\n  width: 50px;\r\n  height: 50px;\r\n  border: 3px solid rgba(0, 212, 170, 0.3);\r\n  border-top: 3px solid #00d4aa;\r\n  border-radius: 50%;\r\n  animation: spin 1s linear infinite;\r\n  margin: 20px 0;\r\n}\r\n\r\n@keyframes spin {\r\n  0% { transform: rotate(0deg); }\r\n  100% { transform: rotate(360deg); }\r\n}\r\n\r\n/* Authentication Page */\r\n.auth-page {\r\n  min-height: 100vh;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  padding: 1rem;\r\n  background: linear-gradient(135deg, #0f0f0f 0%, #1a1a2e 50%, #16213e 100%);\r\n}\r\n\r\n.auth-container {\r\n  background: rgba(0, 0, 0, 0.8);\r\n  backdrop-filter: blur(15px);\r\n  border: 2px solid rgba(0, 212, 170, 0.3);\r\n  border-radius: 15px;\r\n  padding: 2rem;\r\n  max-width: 450px;\r\n  width: 100%;\r\n  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);\r\n}\r\n\r\n/* Mobile responsive adjustments */\r\n@media (max-width: 768px) {\r\n  .auth-page {\r\n    padding: 0;\r\n    align-items: flex-start;\r\n    padding-top: 0.5rem;\r\n  }\r\n  \r\n  .auth-container {\r\n    border-radius: 8px;\r\n    padding: 0.75rem;\r\n    margin: 0;\r\n    min-height: auto;\r\n    max-width: none;\r\n    border-width: 1px;\r\n  }\r\n}\r\n\r\n.auth-header {\r\n  text-align: center;\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n.auth-header h1 {\r\n  font-size: 3rem;\r\n  margin-bottom: 0.5rem;\r\n  background: linear-gradient(45deg, #00d4aa, #64ffda);\r\n  background-clip: text;\r\n  -webkit-background-clip: text;\r\n  -webkit-text-fill-color: transparent;\r\n}\r\n\r\n.auth-header h2 {\r\n  color: #ffffff;\r\n  margin-bottom: 0.5rem;\r\n  font-size: 1.8rem;\r\n}\r\n\r\n.auth-header p {\r\n  color: #b0b0b0;\r\n  font-size: 0.9rem;\r\n}\r\n\r\n/* Mobile auth header adjustments */\r\n@media (max-width: 768px) {\r\n  .auth-header {\r\n    margin-bottom: 1rem;\r\n  }\r\n  \r\n  .auth-header h2 {\r\n    font-size: 1.4rem;\r\n    margin-bottom: 0.3rem;\r\n  }\r\n  \r\n  .auth-header p {\r\n    font-si