/* Skill Info */\r\n.skill-info {\r\n  flex: 1;\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 0.3rem;\r\n}\r\n\r\n.skill-name {\r\n  color: #00d4aa;\r\n  font-size: 1.1rem;\r\n  text-shadow: 0 0 5px rgba(0, 212, 170, 0.3);\r\n  margin: 0;\r\n}\r\n\r\n.skill-description {\r\n  color: #b0b0b0;\r\n  font-size: 0.9rem;\r\n  line-height: 1.4;\r\n  margin: 0;\r\n}\r\n\r\n.skill-controls {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 0.3rem;\r\n  align-items: center;\r\n}\r\n\r\n.skill-btn {\r\n  background: linear-gradient(135deg, rgba(0, 212, 170, 0.1), rgba(0, 212, 170, 0.2));\r\n  border: 2px solid rgba(0, 212, 170, 0.3);\r\n  color: #00d4aa;\r\n  width: 35px;\r\n  height: 35px;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  font-weight: bold;\r\n  font-size: 1.2rem;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  position: relative;\r\n  overflow: hidden;\r\n}\r\n\r\n.skill-btn::before {\r\n  content: '';\r\n  position: absolute;\r\n  top: 0;\r\n  left: -100%;\r\n  width: 100%;\r\n  height: 100%;\r\n  background: linear-gradient(90deg, transparent, rgba(0, 212, 170, 0.4), transparent);\r\n  transition: left 0.5s ease;\r\n}\r\n\r\n.skill-btn:hover::before {\r\n  left: 100%;\r\n}\r\n\r\n.skill-btn:hover {\r\n  background: linear-gradient(135deg, rgba(0, 212, 170, 0.2), rgba(0, 212, 170, 0.4));\r\n  border-color: rgba(0, 212, 170, 0.6);\r\n  transform: scale(1.1);\r\n  box-shadow: 0 4px 15px rgba(0, 212, 170, 0.3);\r\n}\r\n\r\n.skill-btn:active {\r\n  transform: scale(0.95);\r\n}\r\n\r\n.skill-btn:disabled {\r\n  background: rgba(100, 100, 100, 0.1);\r\n  border-color: rgba(100, 100, 100, 0.2);\r\n  color: #666;\r\n  cursor: not-allowed;\r\n  transform: none;\r\n  box-shadow: none;\r\n}\r\n\r\n.skill-btn:disabled::before {\r\n  display: none;\r\n}\r\n\r\n.skill-level-display {\r\n  background: rgba(0, 212, 170, 0.1);\r\n  border: 1px solid rgba(0, 212, 170, 0.3);\r\n  color: #00d4aa;\r\n  padding: 0.3rem 0.6rem;\r\n  border-radius: 12px;\r\n  font-size: 0.9rem;\r\n  font-weight: bold;\r\n  text-align: center;\r\n  min-width: 50px;\r\n}\r\n\r\n/* Modal Styling */\r\n.modal-overlay {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background: rgba(0, 0, 0, 0.8);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  z-index: 1000;\r\n  backdrop-filter: blur(5px);\r\n}\r\n\r\n.modal-content {\r\n  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);\r\n  border: 1px solid rgba(0, 212, 170, 0.3);\r\n  border-radius: 15px;\r\n  padding: 2rem;\r\n  max-width: 500px;\r\n  width: 90%;\r\n  max-height: 80vh;\r\n  overflow-y: auto;\r\n  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.5);\r\n}\r\n\r\n.modal-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 1.5rem;\r\n}\r\n\r\n.modal-header h3 {\r\n  color: #00d4aa;\r\n  font-size: 1.5rem;\r\n}\r\n\r\n.modal-close {\r\n  background: none;\r\n  border: none;\r\n  color: #e0e0e0;\r\n  font-size: 1.5rem;\r\n  cursor: pointer;\r\n  padding: 0.5rem;\r\n  border-radius: 50%;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.modal-close:hover {\r\n  background: rgba(255, 255, 255, 0.1);\r\n  color: #ff6b6b;\r\n}\r\n\r\n/* Form Styling */\r\n.skill-form {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 1rem;\r\n}\r\n\r\n.form-group {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.form-group label {\r\n  color: #00d4aa;\r\n  font-weight: bold;\r\n}\r\n\r\n.form-group input,\r\n.form-group textarea,\r\n.form-group select {\r\n  background: rgba(255, 255, 255, 0.05);\r\n  border: 1px solid rgba(255, 255, 255, 0.2);\r\n  color: #e0e0e0;\r\n  padding: 0.8rem;\r\n  border-radius: 8px;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.form-group input:focus,\r\n.form-group textarea:focus,\r\n.form-group select:focus {\r\n  outline: none;\r\n  border-color: #00d4aa;\r\n  box-shadow: 0 0 10px rgba(0, 212, 170, 0.3);\r\n}\r\n\r\n.form-actions {\r\n  display: flex;\r\n  gap: 1rem;\r\n  justify-content: flex-end;\r\n  margin-top: 1rem;\r\n}\r\n\r\n.btn-primary,\r\n.btn-secondary {\r\n  padding: 0.8rem 1.5rem;\r\n  border: none;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  font-weight: bold;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.btn-primary {\r\n  background: linear-gradient(45deg, #00d4aa, #64ffda);\r\n  color: white;\r\n  box-shadow: 0 4px 15px rgba(0, 212, 170, 0.3);\r\n}\r\n\r\n.btn-primary:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 6px 20px rgba(0, 212, 170, 0.4);\r\n}\r\n\r\n.btn-secondary {\r\n  background: rgba(255, 255, 255, 0.1);\r\n  color: #e0e0e0;\r\n  border: 1px solid rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n.btn-secondary:hover {\r\n  background: rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n/* Health Page Styling */\r\n.health-page {\r\n  animation: fadeIn 0.5s ease-in-out;\r\n}\r\n\r\n.health-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 2rem;\r\n  flex-wrap: wrap;\r\n  gap: 1rem;\r\n}\r\n\r\n.health-header h2 {\r\n  color: #ff6b6b;\r\n  font-size: 2rem;\r\n  text-shadow: 0 0 10px rgba(255, 107, 107, 0.5);\r\n}\r\n\r\n.health-status {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n}\r\n\r\n.status-indicator {\r\n  background: rgba(255, 107, 107, 0.1);\r\n  border: 1px solid rgba(255, 107, 107, 0.3);\r\n  padding: 0.8rem 1.5rem;\r\n  border-radius: 10px;\r\n  backdrop-filter: blur(5px);\r\n}\r\n\r\n.status-label {\r\n  color: #b0b0b0;\r\n  margin-right: 0.5rem;\r\n}\r\n\r\n.status-value {\r\n  color: #ff6b6b;\r\n  font-weight: bold;\r\n  font-size: 1.2rem;\r\n}\r\n\r\n.health-nav {\r\n  display: flex;\r\n  gap: 1rem;\r\n  margin-bottom: 2rem;\r\n  flex-wrap: wrap;\r\n}\r\n\r\n.health-tab {\r\n  background: rgba(255, 255, 255, 0.05);\r\n  border: 1px solid rgba(255, 107, 107, 0.3);\r\n  color: #e0e0e0;\r\n  padding: 0.8rem 1.5rem;\r\n  border-radius: 10px;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.health-tab:hover {\r\n  background: rgba(255, 107, 107, 0.1);\r\n  border-color: rgba(255, 107, 107, 0.5);\r\n}\r\n\r\n.health-tab.active {\r\n  background: linear-gradient(45deg, rgba(255, 107, 107, 0.2), rgba(255, 107, 107, 0.1));\r\n  border-color: #ff6b6b;\r\n  color: #ff6b6b;\r\n}\r\n\r\n/* Health Metrics */\r\n.health-metrics {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 2rem;\r\n}\r\n\r\n.metrics-grid {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\r\n  gap: 1.5rem;\r\n}\r\n\r\n.metric-card {\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border: 1px solid rgba(255, 107, 107, 0.2);\r\n  border-radius: 12px;\r\n  padding: 1.5rem;\r\n  backdrop-filter: blur(10px);\r\n}\r\n\r\n.metric-card h3 {\r\n  color: #ff6b6b;\r\n  margin-bottom: 1rem;\r\n  text-align: center;\r\n  text-shadow: 0 0 10px rgba(255, 107, 107, 0.3);\r\n}\r\n\r\n.metric-row {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 1rem;\r\n  flex-wrap: wrap;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.metric-row label {\r\n  color: #b0b0b0;\r\n  font-weight: bold;\r\n  min-width: 120px;\r\n}\r\n\r\n.metric-row input {\r\n  background: rgba(255, 255, 255, 0.05);\r\n  border: 1px solid rgba(255, 255, 255, 0.2);\r\n  color: #e0e0e0;\r\n  padding: 0.5rem;\r\n  border-radius: 6px;\r\n  max-width: 100px;\r\n}\r\n\r\n.bp-inputs {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.bp-inputs span {\r\n  color: #ff6b6b;\r\n  font-weight: bold;\r\n}\r\n\r\n.slider-container {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n  flex: 1;\r\n}\r\n\r\n.wellness-slider {\r\n  flex: 1;\r\n  height: 6px;\r\n  border-radius: 3px;\r\n  outline: none;\r\n  background: rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.wellness-slider.stress::-webkit-slider-thumb,\r\n.wellness-slider.energy::-webkit-slider-thumb {\r\n  appearance: none;\r\n  width: 20px;\r\n  height: 20px;\r\n  border-radius: 50%;\r\n  background: linear-gradient(45deg, #ff6b6b, #ff8e8e);\r\n  cursor: pointer;\r\n  box-shadow: 0 0 10px rgba(255, 107, 107, 0.5);\r\n}\r\n\r\n.slider-value {\r\n  color: #ff6b6b;\r\n  font-weight: bold;\r\n  min-width: 20px;\r\n  text-align: center;\r\n}\r\n\r\n.metric-result {\r\n  text-align: center;\r\n  margin-top: 1rem;\r\n  padding: 0.8rem;\r\n  background: rgba(255, 107, 107, 0.1);\r\n  border-radius: 8px;\r\n}\r\n\r\n/* Export Page Styling */\r\n.export-page {\r\n  animation: fadeIn 0.5s ease-in-out;\r\n}\r\n\r\n.export-header h2 {\r\n  color: #ffd700;\r\n  font-size: 2rem;\r\n  text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.format-grid {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\r\n  gap: 1rem;\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n.format-card {\r\n  background: rgba(255, 255, 255, 0.05);\r\n  border: 1px solid rgba(255, 215, 0, 0.3);\r\n  border-radius: 10px;\r\n  padding: 1rem;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  text-align: center;\r\n}\r\n\r\n.format-card:hover {\r\n  background: rgba(255, 215, 0, 0.1);\r\n  border-color: rgba(255, 215, 0, 0.5);\r\n}\r\n\r\n.format-card.selected {\r\n  background: linear-gradient(45deg, rgba(255, 215, 0, 0.2), rgba(255, 215, 0, 0.1));\r\n  border-color: #ffd700;\r\n}\r\n\r\n.format-card h4 {\r\n  color: #ffd700;\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.export-actions {\r\n  display: flex;\r\n  gap: 1rem;\r\n  justify-content: center;\r\n  margin: 2rem 0;\r\n  flex-wrap: wrap;\r\n}\r\n\r\n.btn-ai {\r\n  background: linear-gradient(45deg, #9c27b0, #e91e63);\r\n  color: white;\r\n  padding: 0.8rem 1.5rem;\r\n  border: none;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  font-weight: bold;\r\n  transition: all 0.3s ease;\r\n  box-shadow: 0 4px 15px rgba(156, 39, 176, 0.3);\r\n}\r\n\r\n.btn-ai:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 6px 20px rgba(156, 39, 176, 0.4);\r\n}\r\n\r\n/* Animations */\r\n@keyframes fadeIn {\r\n  from {\r\n    opacity: 0;\r\n    transform: translateY(20px);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    transform: translateY(0);\r\n  }\r\n}\r\n\r\n@keyframes drawCircle {\r\n  from {\r\n    stroke-dasharray: 0 1000;\r\n  }\r\n  to {\r\n    stroke-dasharray: var(--progress) 1000;\r\n  }\r\n}\r\n\r\n@keyframes sparkle {\r\n  0%, 100% {\r\n    transform: translate(-50%, -50%) scale(1);\r\n    opacity: 1;\r\n  }\r\n  50% {\r\n    transform: translate(-50%, -50%) scale(1.2);\r\n    opacity: 0.8;\r\n  }\r\n}\r\n\r\n/* Responsive Design */\r\n@media (max-width: 768px) {\r\n  .app-content {\r\n    padding: 0;\r\n  }\r\n  \r\n  .app-title {\r\n    font-size: 2rem;\r\n  }\r\n  \r\n  .tab-navigation {\r\n    gap: 0.5rem;\r\n    padding: 0 0.5rem;\r\n  }\r\n  \r\n  .tab-button {\r\n    padding: 0.6rem 1rem;\r\n    font-size: 0.9rem;\r\n    border-radius: 8px;\r\n  }\r\n  \r\n  .tab-button:hover {\r\n    transform: none;\r\n  }\r\n  \r\n  .tab-icon {\r\n    font-size: 1rem;\r\n  }\r\n  \r\n  .skills-header {\r\n    flex-direction: column;\r\n    text-align: center;\r\n  }\r\n  \r\n  .skills-stats {\r\n    justify-content: center;\r\n  }\r\n  \r\n  .skills-list {\r\n    grid-template-columns: 1fr;\r\n  }\r\n  \r\n  .metrics-grid {\r\n    grid-template-columns: 1fr;\r\n  }\r\n  \r\n  .export-actions {\r\n    flex-direction: column;\r\n    align-items: center;\r\n  }\r\n}\r\n\r\n/* Scrollbar Styling */\r\n::-webkit-scrollbar {\r\n  width: 8px;\r\n}\r\n\r\n::-webkit-scrollbar-track {\r\n  background: rgba(255, 255, 255, 0.05);\r\n  border-radius: 4px;\r\n}\r\n\r\n::-webkit-scrollbar-thumb {\r\n  background: linear-gradient(45deg, #00d4aa, #64ffda);\r\n  border-radius: 4px;\r\n}\r\n\r\n::-webkit-scrollbar-thumb:hover {\r\n  background: linear-gradient(45deg, #64ffda, #00d4aa);\r\n}\r\n\r\n/* Additional Health Components */\r\n.body-visualization {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 1.5rem;\r\n  margin-top: 2rem;\r\n}\r\n\r\n.body-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 1.5rem;\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border: 1px solid rgba(255, 107, 107, 0.2);\r\n  border-radius: 12px;\r\n  padding: 1.5rem;\r\n  backdrop-filter: blur(10px);\r\n  max-width: 100%;\r\n  overflow: hidden;\r\n}\r\n\r\n.body-diagram {\r\n  text-align: center;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n.body-svg {\r\n  width: 100%;\r\n  max-width: 300px;\r\n  height: auto;\r\n}\r\n\r\n.body-part {\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.body-part:hover {\r\n  filter: drop-shadow(0 0 8px currentColor);\r\n}\r\n\r\n.body-part.selected {\r\n  filter: drop-shadow(0 0 12px currentColor);\r\n  stroke-width: 2;\r\n}\r\n\r\n.body-legend {\r\n  margin-top: 1rem;\r\n}\r\n\r\n.legend-items {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  gap: 1rem;\r\n  justify-content: center;\r\n}\r\n\r\n.legend-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.legend-color {\r\n  width: 16px;\r\n  height: 16px;\r\n  border-radius: 50%;\r\n}\r\n\r\n.legend-color.good { background: #4CAF50; }\r\n.legend-color.minor { background: #FFC107; }\r\n.legend-color.moderate { background: #FF9800; }\r\n.legend-color.severe { background: #F44336; }\r\n\r\n.body-part-detail {\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border: 1px solid rgba(255, 107, 107, 0.2);\r\n  border-radius: 12px;\r\n  padding: 1.5rem;\r\n  backdrop-filter: blur(10px);\r\n  position: fixed;\r\n  right: 2rem;\r\n  top: 50%;\r\n  transform: translateY(-50%);\r\n  width: 350px;\r\n  max-height: 80vh;\r\n  overflow-y: auto;\r\n  z-index: 1000;\r\n  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);\r\n  display: none; /* Hide the old floating version */\r\n}\r\n\r\n.body-part-detail-integrated {\r\n  back